import { ElNotification } from "element-plus";

/**
 * 
 * @param {*} message 
 * @param {*} select 
 */
export default function (message: any, select: any) {
    let title: string;
    let type: any;
    switch (select) {
        case 1:
            title = "";
            type = "success";
            break;
        case 2:
            title = "";
            type = "warning";
            break;
        case 3:
            title = "";
            type = "info";
            break;
        default:
            title = "";
            type = "error";
            break;
    }
    ElNotification({
        // duration: 0,
        title,
        message,
        type
    });
}
