import data from "@/data/data";
import local from "@/utils/localData/local";

export default {
    /**
     * 
     * @param color 
     */
    change(color?: string) {
        if (color) {
            data.saveData.themeColor = color;
        }
        document.documentElement.style.setProperty(
            "--themeColor",
            data.saveData.themeColor
        );
        local.save();
    }
};
